[class*="-main"] {
  width: 38.75em; }

[class*="-aside"] {
  width: 18.75em; }

[class*="-left"] {
  float: left;
  margin-right: 1.25em; }

[class*="-right"] {
  float: right; }

[class*="-center"] {
  margin: 0 auto; }

.login-box [class*="login-form"] {
