/* This is Hell on earth */
@media screen and (min-width: 58.75em) {
  .main-wrapper {
    width: 58.75em;
    margin: 0 auto; } }

@media screen and (max-width: 58.6875em) {
  .main-wrapper {
    width: 100%;
    margin: 0 auto; } }

@media screen and (max-width: 52.5em) {
  #header .logo {
    left: 1.8125em; } }

@media screen and (max-width: 25em) {
  [class*="-left"],
  [class*="-right"] {
    float: none;
    margin-right: 0;
    margin-left: 0; }
  [class*="-center"] {
    margin: 0 auto; }
  [class*="-main"],
  [class*="-aside"] {
    width: 38.75em; }
  [class*="-aside"] {
    width: auto;
    margin: 0 0.625em; }
  .tactic .main-wrapper {
    width: auto;
    margin: 0 0.625em; }
  .tactic .tactic-item {
    float: none;
    margin: 0.625em 0 0 0; }
    .tactic .tactic-item:first-of-type {
      margin: 0; }
  .work-us {
    @inclue hide(); } }
