body {
  font-family: "Avenir Heavy";
  background: #f2f4f5; }

form label {
  display: block;
  font-size: 0.875em;
  line-height: 1.3571428571em; }

form .text-box {
  font-size: 0.875em;
  border: solid 0.0714285714em #8cc8eb;
  box-sizing: border-box;
  height: 2em;
  line-height: 1.3571428571em;
  padding: 0 1em;
  margin-top: 0.3571428571em; }

.afterload {
  background: url(../images/logo-atyca.svg) no-repeat center center #336699;
  background-size: 60% 40%;
  text-indent: -999em; }

[class*="button"] {
  font-size: 0.625em;
  line-height: 3.3em;
  height: 3.3em;
  font-family: "Avenir Black";
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  display: block; }

.menu-container .menu-item {
  float: left;
  width: 7.375em;
  margin-left: 0.5em; }
  .menu-container .menu-item:first-of-type {
    margin-left: 0; }

.menu-container .menu-link {
  display: block;
  width: 100%;
  height: 100%;
  text-align: center;
  color: white;
  font-size: 0.875em; }

.menu-container .selected,
.menu-container .menu-link:hover {
  border-radius: 0.5em;
  background: #265686; }

#header {
  overflow: hidden; }
  #header .logo {
    display: block;
    position: absolute;
    height: 6.9375em;
    width: 9.5em;
    left: 0;
    background: url(../images/logo-atyca.svg) no-repeat center center;
    top: 50%;
    margin-top: -3.4375em;
    text-indent: -9999em; }
    #header .logo:before {
      content: "Operador Mayorista";
      position: absolute;
      font-size: 0.6875em;
      line-height: 1em;
      color: #8cc8eb;
      text-indent: 0;
      bottom: 1em;
      left: 8.7272727273em;
      width: 10em; }

#footer {
  text-align: center;
  padding: 2.25em 0 1.25em 0;
  color: white; }
  #footer .footer-title {
    font-size: 0.75em;
    line-height: 1.3333333333em; }
  #footer .footer-title,
  #footer .footer-highlight {
    color: #8cc8eb; }
  #footer p {
    font-size: 0.625em;
    line-height: 1.6em; }
